import * as React from "react";
import Layout from "../components/layout";
import HeroAnimation from "../components/home/heroAnimation";
import HeroProductOverview from "../components/home/heroProductOverview";
import ContactUs from "../components/contactUs";

const WorkPage = () => {
  return (
    <Layout pageTitle="Our Work">
      <HeroAnimation>
        <div className="text-center p-2 vh-50">
          <p className="fs-2 p-4 text-wrap">
            We absorb your ideas and conjure advertising strategies that propel
            your business into the online universe. Content creation, web
            design, brand building are some things we are passionate about. We
            don't do just 3D rendering, animations and corporate branding, we
            make magic.
          </p>
        </div>
      </HeroAnimation>
      <HeroAnimation>
        <HeroProductOverview isPage="work" />
      </HeroAnimation>
      <ContactUs />
    </Layout>
  );
};

export default WorkPage;

export const Head = () => <title>Our Work</title>;
